<template>
  <transition name="delay">
    <div v-show="modal" class="modal">
      <!-- Modal background-->
      <transition name="fade">
        <div
          v-if="modal"
          @click.stop.prevent="modal = false"
          class="modal-background"
        ></div>
      </transition>
      <!-- Modal dialog -->
      <transition name="slide">
        <div
          v-if="modal"
          @click.stop.prevent="modal = true"
          class="modal-dialog d-flex flex-column"
        >
          <div class="modal-header">
            <button
              @click.stop.prevent="modal = false"
              type="button"
              class="close d-flex justify-content-center align-items-center"
            >
              <!-- SVG -->
              <svg
                width="15"
                height="15"
                viewBox="0 0 15 15"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M9.41387 7.99988L15.2069 2.20687C15.5969 1.81687 15.5969 1.18388 15.2069 0.792875C14.8169 0.401875 14.1839 0.402875 13.7929 0.792875L7.99988 6.58588L2.20687 0.792875C1.81687 0.402875 1.18388 0.402875 0.792875 0.792875C0.401875 1.18288 0.402875 1.81587 0.792875 2.20687L6.58588 7.99988L0.792875 13.7929C0.402875 14.1829 0.402875 14.8159 0.792875 15.2069C0.987875 15.4019 1.24287 15.4999 1.49987 15.4999C1.75687 15.4999 2.01187 15.4019 2.20687 15.2069L7.99988 9.41387L13.7929 15.2069C13.9879 15.4019 14.2429 15.4999 14.4999 15.4999C14.7569 15.4999 15.0119 15.4019 15.2069 15.2069C15.5969 14.8169 15.5969 14.1839 15.2069 13.7929L9.41387 7.99988Z"
                  fill="#FF6600"
                />
              </svg>
              <!-- SVG -->
            </button>
          </div>
          <hr />
          <!-- modal-body -->
          <div class="modal-body d-flex flex-column">
            <!-- tweet -->
            <div class="tweet d-flex">
              <div class="avatar">
                <SkeletonScreen :isLoading="isLoading" :skeleton="'div'">
                  <template #originalNode>
                    <router-link :to="`/users/${id}`"
                      ><img :src="avatar | nullAvatar" alt="" />
                    </router-link>
                  </template>
                </SkeletonScreen>
              </div>

              <div class="tweet-body">
                <div class="d-flex">
                  <h3 class="name">
                    <SkeletonScreen
                      :isLoading="isLoading"
                      :skeleton="'span'"
                      :skeletonLength="6"
                    >
                      <template #originalNode
                        ><router-link :to="`users/${id}`">{{
                          name
                        }}</router-link>
                      </template>
                    </SkeletonScreen>
                  </h3>
                  <span class="info">
                    <SkeletonScreen
                      :isLoading="isLoading"
                      :skeleton="'span'"
                      :skeletonLength="3"
                    >
                      <template #originalNode>
                        <router-link :to="`users/${id}`"
                          >@{{ account }}</router-link
                        >
                      </template> </SkeletonScreen
                    ><SkeletonScreen
                      :isLoading="isLoading"
                      :skeleton="'span'"
                      :skeletonLength="2"
                    >
                      <template #originalNode>
                        <router-link v-if="!isLoading" :to="`users/${id}`">{{
                          createdAt | fromNow
                        }}</router-link>
                      </template>
                    </SkeletonScreen>
                  </span>
                </div>

                <div class="tweet-content">
                  <SkeletonScreen
                    :isLoading="isLoading"
                    :skeleton="'div'"
                    :skeletonLine="2"
                  >
                    <template #originalNode>
                      <span>{{ description }}</span>
                    </template>
                  </SkeletonScreen>
                </div>

                <span class="reply-to"
                  >&thinsp;<SkeletonScreen
                    :isLoading="isLoading"
                    :skeleton="'span'"
                    :skeletonLength="3"
                  >
                    <template #originalNode>
                      <router-link :to="`users/${id}`"
                        >@{{ account }}</router-link
                      >
                    </template>
                  </SkeletonScreen>
                </span>
              </div>
            </div>
            <!-- reply -->
            <div class="reply d-flex">
              <div class="avatar">
                <transition name="skeleton" mode="out-in">
                  <router-link to="/profile">
                    <img
                      :src="currentUser.avatar | nullAvatar"
                      alt=""
                    />
                  </router-link>
                </transition>
              </div>

              <textarea
                v-model="reply"
                class="reply-content"
                placeholder=""
                maxlength="140"
                autofocus
              ></textarea>
            </div>
          </div>
          <!-- modal-footer -->
          <div
            class="modal-footer d-flex justify-content-end align-items-center"
          >
            <transition name="hint">
              <span v-if="replyHint" class="hint">{{
                replyHint === 'empty' ? '' : ' 140 '
              }}</span>
              ></transition
            >
            <button @click="submitReply" :disabled="isProcessing" type="button">
              
            </button>
          </div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
import { fromNowFilter, nullAvatarFilter } from '../utils/mixins'
import tweetAPI from '../apis/tweet'
import replyAPI from '../apis/reply'
import { mapState } from 'vuex'
import SkeletonScreen from '../components/slot/SkeletonScreen'

export default {
  components: { SkeletonScreen },
  mixins: [fromNowFilter, nullAvatarFilter],
  data() {
    return {
      modal: false,
      isLoading: true,
      tweetId: -1,
      // 
      id: -1,
      avatar: '',
      name: '',
      account: '',
      createdAt: '',
      description: '',
      // 
      reply: '',
      replyHint: false,
      isProcessing: false
    }
  },

  computed: {
    ...mapState(['currentUser']),
    replyCount() {
      if (this.reply) {
        this.reply.length >= 140 && (this.replyHint = true)
        this.reply.length <= 140 && (this.replyHint = false)
        return this.reply.length
      } else {
        return 0
      }
    }
  },

  watch: {
    reply(nowState) {
      nowState.length >= 140
        ? (this.replyHint = true)
        : (this.replyHint = false)
    }
  },

  methods: {
    async fetchTweet(tweetId) {
      this.isLoading = true
      try {
        const { data } = await tweetAPI.fetchTweet({ tweetId })
        this.id = data.userId
        this.avatar = data.User.avatar
        this.name = data.User.name
        this.account = data.User.account
        this.createdAt = data.createdAt
        this.description = data.description

        this.isLoading = false
      } catch (error) {
        console.log(error)
        this.$bus.$emit('toast', {
          icon: 'error',
          title: `${error}`
        })
        this.isLoading = false
      }
    },

    async submitReply() {
      try {
        if (!this.reply || !this.reply.trim().length) {
          this.replyHint = 'empty'
          this.$bus.$emit('toast', { icon: 'error', title: '' })
          return
        }

        if (this.reply && this.reply.length >= 140) {
          this.replyHint = true
          this.$bus.$emit('toast', {
            icon: 'error',
            title: ' 140 '
          })
          return
        }

        this.isProcessing = true

        const { data } = await replyAPI.subMitReply({
          tweetId: this.tweetId,
          comment: this.reply
        })
        console.log('', data)

        if (data.status === 'success') {
          this.$bus.$emit('toast', {
            icon: 'success',
            title: ''
          })
        } else {
          throw new Error(data.message)
        }
        this.modal = false
        this.isProcessing = false
        this.reply = ''
      } catch (error) {
        console.log(error)
        this.isProcessing = false
        this.$bus.$emit('toast', {
          icon: 'error',
          title: `${error}`
        })
      }
    }
  },

  mounted() {
    this.$bus.$on('replyModal', (tweetId) => {
      this.fetchTweet(tweetId)
      this.tweetId = tweetId
      this.modal = true
    })
  }
}
</script>

